import React from 'react';
import { Link, graphql } from 'gatsby';
import Layout from "../components/layout"
import Hero from '../components/shared/hero';
import BottomCta from '../components/shared/bottom-cta';
import CollapsibleSection from '../components/shared/collapsable';
import ScrollAnimation from 'react-animate-on-scroll';
import VerticalSteps from '../components/shared/vertical-steps'

import '../styles/home.css';
import SEO from '../components/seo';
import constants from './../constants/home'

import leftImageSteps from '../images/home/vertical-steps.svg'
import PackageCta from '../components/home/package-cta';
import ImageSection from '../components/home/image-section';
import PackageSection from '../components/home/package-section';
import ChannelsSection from '../components/home/channels-section';
import ReviewsSection from '../components/home/reviews-section';
import HelpCardsSection from '../components/home/help-cards-section';

const HomePage = ({ data }) => {

  return (
    <>
      <div className='home-page'>
        <Layout
          activeTab={'/za-doma'}
        >
          <SEO title='За Дома' />
          <Hero
            heroImage={data.heroImage.childImageSharp.fluid.src}
            imageClass='home-packages-hero'
            title={(<h1 className='text-white mt-3'>Нашите пакети – по-добри<br />от тези под коледната елха</h1>)}
          />
          
          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <PackageSection />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <PackageCta
              bgImg={data.bgImage.childImageSharp.fluid.src}
            >
              <p className='mb-md-5 mb-3 text-white'>Търсиш нещо по-така?</p>
              <h2 className='mb-md-5 mb-2 text-white'>Създай пакет по свой <br />образ и (без)подобие!</h2>
              <Link to={'/configurator'} className='btn btn-primary green mt-5 mt-md-0'>Създай свой пакет</Link>
            </PackageCta>
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <ImageSection />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <ChannelsSection />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <HelpCardsSection />
          </ScrollAnimation>


          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <BottomCta
              bgColor={'transparent'}>
              <h2 className='mb-md-5 mb-2 d-none d-md-block'>Нямаме търпение да ти покажем<br /><span className='text-purple'> колко сме добри!</span></h2>
              <h2 className='mb-md-5 mb-2 d-block d-md-none'>Нямаме търпение да ти покажем<span className='text-purple'> колко сме добри!</span></h2>
              <p className='mb-md-5 mb-3'>Нашите пакети – по-благи от тези под коледната елха</p>
              <Link to={'/za-doma'} className='btn btn-primary green'>Виж пакетите</Link>
            </BottomCta>
          </ScrollAnimation>


          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <VerticalSteps
              title={<h2>Трите крачки към<br />NetSurf удоволствието</h2>}
              isDark={false}
              steps={constants.verticalSteps}
              leftImage={leftImageSteps}
            />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <ReviewsSection />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <CollapsibleSection
              isDark={false}
              faqs={constants.faqs}
              sectionTitle={(
                <>
                  <h2 style={{ whiteSpace: 'pre-wrap' }}>Често задавани въпроси</h2>
                  <hr className='center' />
                </>
              )}
            />
          </ScrollAnimation>

        </Layout>
      </div>

    </>
  )
}

export const query = graphql`
  query HomePageQuery {
    bgImage: file(relativePath: {eq: "home/cta-bg.png"}) {
      childImageSharp {
        fluid(maxHeight: 200) {
          src
        }
      }
    }
    heroImage: file(relativePath: {eq: "home/home-packages-hero.png"}) {
      childImageSharp {
        fluid(maxHeight: 800) {
          src
        }
      }
    } 
  }`

export default HomePage;