import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import Slider from "react-slick";

import facebookLogo from '../../images/home/facebook-review.svg';
import googleLogo from '../../images/home/google-review.svg';
import constants from '../../constants/home';
import { useStaticQuery, graphql } from 'gatsby';

const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bgImg});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; 
  display: flex;
  justify-content: center;
  align-items: center;
  /* min-height: 600px; */
  min-height: 1600px;
  /* background: black; */
`;

const SliderBubble = styled.div`
  position: absolute;
  top: 25%;
  left: -8%;
  background: #158FCE;
  border-top-left-radius: 90%;
  border-top-right-radius: 60%;
  border-bottom-left-radius: 60%;
  border-bottom-right-radius: 120%;
  width: 140px;
  height: 140px;
  display: flex;
  justify-content: center;
  align-items:center;
  z-index: 1;
`;

const SliderBubbleSm = styled.div`
  position: absolute;
  top: -25%;
  /* left: 40%; */
  /* transform: translate(0, -50%); */
  background: #158FCE;
  border-top-left-radius: 90%;
  border-top-right-radius: 60%;
  border-bottom-left-radius: 60%;
  border-bottom-right-radius: 120%;
  width: 140px;
  height: 140px;
  display: flex;
  justify-content: center;
  align-items:center;
  z-index: 1;
`;

const SliderBox = styled.div`
  position: relative;
  width: 700px;
  padding: 2rem 5rem 1rem 5rem;
  background: #FFFFFF;
  box-shadow: 0px 0px 28.5883px rgba(17, 6, 29, 0.2);
  border-radius: 6.01859px;
  transform: scale(0.75);
`;

const SliderBoxSm = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  min-width: 300px;
  max-width: 470px;
  padding: 5rem 2rem 1rem 2rem;
  margin: 5rem 0rem 1rem 0rem;
  background: #FFFFFF;
  box-shadow: 0px 0px 28.5883px rgba(17, 6, 29, 0.2);
  border-radius: 6.01859px;
  transform: scale(0.95);
`;

const ReviewsSection = () => {
  let sliderRef = useRef(null);
  let rowRef = useRef(null);
  const [windowSize, setWindowSize] = useState(0);

  const data = useStaticQuery(graphql`
    query ReviewsPackagesQuery {
      bgImage: file(relativePath: {eq: "home/home-reviews-bg.png"}) {
        childImageSharp {
          fluid {
            src
          }
        }
      }
  }`);

  let rowRefOnRemove = null;
  useEffect(() => {
    onResizeListener(null);
    window.addEventListener('resize', onResizeListener);
    rowRef.current.addEventListener('wheel', onScrollSlider);
    rowRefOnRemove = rowRef;
    return () => {
      window.removeEventListener('resize', onResizeListener);
      rowRefOnRemove.current.removeEventListener('wheel', onScrollSlider);
    }
  }, [])

  const onScrollSlider = (e) => {
    if (window.innerWidth >= 992) {
      e.preventDefault();
      e.deltaY > 0 ? sliderRef.current.slickNext() : sliderRef.current.slickPrev();
      console.log('wheel slick');
    }
  }

  const onResizeListener = (_) => setWindowSize(window.innerWidth)

  let cards = constants.reviews.map((review, index) => (
    <div key={index} className='mx-lg-3 d-flex justify-content-center'>
      <Card review={review} windowSize={windowSize} />
    </div>
  ))
  return (
    <>
      <section id='reviews-home-packages' className='centering spacing-top spacing-bottom'>
        {/* <BackgroundWrapper bgImg={data.bgImage.childImageSharp.fluid.src}> */}
          <div className='row mx-xl-5 d-flex'>
            <div className='col-12 col-lg-8 mt-5 mt-lg-0 home-packages-reviews order-1 order-lg-0 reasons-slider' ref={rowRef}>
              <Slider {...settings} ref={sliderRef}>
                {cards}
              </Slider>
            </div>
            <div className='col-12 col-lg-4 d-flex justify-content-center align-items-center order-0 order-lg-1'>
              <div className='col-lg-12 col-xl-9 col-md-6 p-0'>
                <h2 className='text-center text-lg-left'>Задоволството ти е водещият ни мотив да се подобряваме непрекъснато</h2>
                <hr className={windowSize >= 992 ? 'left' : 'center'} />
                <p>Ние работим здраво и се развиваме всеки ден, за да се наслаждаваш ти на все по-висококачествени услуги. Клиентите ни не лъжат.</p>
                <div className='row'>
                  <div className='col-6 py-3 d-flex justify-content-center align-items-center reviews-border right'>
                    <div className='text-center'>
                      <h2 className='review'><span className='text-blue'>4.9</span></h2>
                      <Stars count={5} />
                      <img src={facebookLogo} className='img-fluid mt-2' alt='Ревюта във Facebook' />
                    </div>
                  </div>
                  <div className='col-6 py-3 d-flex justify-content-center align-items-center reviews-border'>
                    <div className='text-center'>
                      <h2 className='review'><span className='text-blue'>4.9</span></h2>
                      <Stars count={5} />
                      <img src={googleLogo} className='img-fluid mt-2' alt='Ревюта в Google' />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        {/* </BackgroundWrapper> */}
      </section>
    </>
  );
}

const Card = ({ review, windowSize }) => {
  const { title, initials, name, comment, city } = review;

  if (windowSize >= 992) {
    return (
      <SliderBox className='review-card'>
        <SliderBubble>
          <h2 className='review text-white'>{initials}</h2>
        </SliderBubble>
        <h3>{title}</h3>
        <div className='pl-5'>
          <p>{comment}</p>
          <p><strong>{name}</strong> | <span className='text-grey'>{city}</span></p>
        </div>
      </SliderBox>
    )
  }

  return (
    <SliderBoxSm className='review-card text-center'>
      <SliderBubbleSm>
        <h2 className='review text-white'>{initials}</h2>
      </SliderBubbleSm>
      <div>
        <h3>{title}</h3>
        <p>{comment}</p>
        <p><strong>{name}</strong> | <span className='text-grey'>{city}</span></p>
      </div>
    </SliderBoxSm>
  )
}

const Stars = ({ count }) => {
  let starList = [];
  for (var i = 0; i < count; i++) {
    starList.push(<span key={i} className='icon package star '></span>);
  }
  return (
    <div className='d-block'>
      {starList}
    </div>);
}

const settings = {
  autoplay: true,
  autoplaySpeed: 4000,
  centerMode: true,
  dots: false,
  speed: 1000,
  arrows: false,
  infinite: true,
  vertical: true,
  slidesToShow: 3,
  slidesToScroll: 1,
  focusOnSelect: true,
  pauseOnFocus: true,
  responsive: [
    {
      breakpoint: 991,
      settings: {
        slidesToShow: 1,
        vertical: false,
        infinite: false,
        dots: true,
        centerMode: false,
      }
    }
  ]

}

export default ReviewsSection;