import React from 'react';
import SectionTitle from '../shared/section-titles';
import ImageTextSection from '../index/image-text';
import { graphql, useStaticQuery } from 'gatsby';

import constants from './../../constants/home';

const ImageSection = ({ }) => {
  const data = useStaticQuery(graphql`
    query HomePageImageTextQuery {
      forInternet: file(relativePath: {eq: "home/tv.png"}) {
        childImageSharp {
          fluid (maxHeight: 700) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      forTV: file(relativePath: {eq: "home/net.png"}) {
        childImageSharp {
          fluid (maxHeight: 700) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  return (
    <>
      <section id='home-image-text'>
        <SectionTitle>
          <h2 style={{ whiteSpace: 'pre-wrap' }}>Защо да избереш NetSurf</h2>
          <hr className='center ' />
        </SectionTitle>

        <ImageTextSection
          imgSrc={data.forInternet.childImageSharp.fluid}
          imgAlt={'Надежден интернет'}
          sectionTitle={<h2>Заради нашия<br /><span className='text-purple'>надежден и бърз интернет</span></h2>}
          undertext={constants.forInternet.text}
          list={constants.forInternet.list}
          order={true}
        />

        <ImageTextSection
          imgSrc={data.forTV.childImageSharp.fluid}
          imgAlt={'Телевизия, която владее времето'}
          sectionTitle={<h2>Защото<br /><span className='text-purple'>телевизията ни владее времето</span><br />и ви събира заедно</h2>}
          undertext={constants.forTV.text}
          list={constants.forTV.list}
          order={false}
        />
      </section>
    </>
  );
}

export default ImageSection;